/**
 * @title  
 * @desc ,  closable
 */
@Component({
  components: {
  },
  })
export default class ClosableExample extends Vue {
  value: string = '1'

  position: string = 'top'

  type="card"

  tabs: any[] = []

  index: number = 10

  add () {
    let i = this.index++
    this.tabs.push({
      name: i + '',
      label: 'Label ' + i
    })
  }
}
