ion>\n      <v-option value=\"wuhan\" label=\"\"></v-option>\n      <v-option value=\"chengdu\" label=\"\"></v-option>\n      <v-option value=\"hangzhou\" label=\"\"></v-option>\n      <v-option value=\"suzhou\" label=\"\"></v-option>\n    </v-select>\n  </div>\n</div>\n</template>\n<script lang=\"ts\">\nimport { Component, Vue, Watch } from 'vue-property-decorator'\n\n/**\n * @title  \n * @desc  Tag \n */\n@Component({\n  components: {\n  },\n  })\nexport default class GroupExample extends Vue {\n  value: string[] = ['shanghai']\n\n  size: string = 'md'\n\n  value1: string[] = []\n\n  value2: string[] = []\n\n  value3: string[] = []\n\n  clob: string[] = [\n    'a', 'a1', 'a2', 'a3', 'a4',\n    'b', 'b1', 'b2', 'b3', 'b4',\n    'c', 'c1', 'c2', 'c3', 'c4',\n  ]\n\n  searchFn (input: string, cb: (items: any[])=>void) {\n    setTimeout(() => {\n      let ret = this.clob.filter(v => v.includes(input)).map(v => {\n        return {\n          label: v,\n          value: v\n        }\n      })\n      cb(ret)\n    }, 2000)\n  }\n}\n</script>\n"